<template>
    <div class="header_container">
        <div class="header_text">
            <h1 :style="'user-select: none;'">Ваши идеи, моя реальность!</h1>
        </div>
        <div class="header_img"></div>
    </div>
</template>

<script>
export default {
    name: "Header",
    data() {
        return {

        }
    }
}
</script>

<style scoped>
.header_container{
    display: flex;
    justify-content: center;
    padding: 2% 5% 5% 5%;
}
.header_img{
    background-image: url("https://sun9-50.userapi.com/impf/23ZftJIcWofB-XKvSzaWagfJB4n3HjgTNKC-Hw/WUsq9Ukvx2U.jpg?size=1200x705&quality=95&sign=c4b1b8fc917b6a234c6a7243f2ebba53&type=album");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-height: 300px;
    min-width: 50%;
}
.header_text{
    margin-top: 2%;
    background-image: url("https://sun9-49.userapi.com/impf/seaA3pheebJsB1eDhchxQjPHFDE0UW5EjxU7lQ/33bgXjZSIdw.jpg?size=700x500&quality=95&sign=f899c87e4f792c1aac0845cba4cd2cae&type=album");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    text-align: left;
}
@media (max-width: 740px) {
    .header_container{
        display: block;
    }
    .header_text{
        margin-top: 0%;
        background-image: none;
    }
}
</style>


